import BestCase from "components/base/bestcase/BestCase";
import Brand from "components/base/brand/Brand";
import Footer from "components/base/footer/Footer";
import Introduce from "components/base/introduce/Introduce";
import OurGame from "components/base/ourgame/OurGame";
import ScrollVideo from "components/base/scrollVideo/ScrollVideo";
import Strength from "components/base/strength/Strength";
import Value from "components/base/value/Value";
import React from "react";
import { useLocalizePage } from "store/actions/localizeAction";
import "./Home.scss";

const Home: React.FC = () => {
  useLocalizePage("home", "/api/page/en/home?includeBaseData=1");

  return (
    <div className="com-home animated fadeIn">
      <Introduce ishomepage="true" scrollable="true" />
      <Value />
      <ScrollVideo />
      <Strength showArrow={false} keyIndex={null} />
      <BestCase />
      <Brand />
      <OurGame />
      <Footer />
    </div>
  );
};

export default Home;
